import React, { useState } from 'react'
import './App.css'
import { useQuery } from '@apollo/client'
import { GET_SPLITTED_LIST } from './queries/queries'
import loadingAnimation from './resources/loading.gif'
import TopicSelection from './components/TopicSelection'
import Navbar from './components/Navbar'
import OldTopics from './components/OldTopics'
import AddTopic from './components/AddTopic'

const App = () => {

  const [body, setBody] = useState('main')

  const pool = useQuery(GET_SPLITTED_LIST)

  if (pool.loading) {
    return (
      <div className='App'>
        <div className='Container'>
          <img className='Loading-animation' src={loadingAnimation} alt='loading animation' />
        Loading...
      </div>
      </div>
    )
  }

  if (body === 'main') {
    return (
      <div className="App">
        <Navbar setBody={setBody} />
        <TopicSelection leftList={pool.data.getSplittedList.leftList.topics} rightList={pool.data.getSplittedList.rightList.topics} />
      </div>
    )
  }

  if (body === 'covered') {
    return (
      <div className='App'>
        <Navbar setBody={setBody} />
        <OldTopics />
      </div>
    )
  }

  if (body === 'addTopic') {
    return (
      <div className='App'>
        <Navbar setBody={setBody} />
        <AddTopic />
      </div>
    )
  }

}

export default App
