import React from 'react'
import { useQuery } from '@apollo/client'
import loadingAnimation from '../resources/loading.gif'
import { GET_COVERED } from '../queries/queries'

const OldTopics = () => {

    const result = useQuery(GET_COVERED)

    if (result.loading) {
        return (
            <div className='App'>
                <div className='Container'>
                    <img className='Loading-animation' src={loadingAnimation} alt='loading animation' />
                    <p>Loading...</p>
                </div>
            </div>
        )
    }

    const topics = result.data.getCovered

    return (
        <div>
            <div className='Container'>
                <h1 className='Heading'>Käsitellyt Aiheet</h1>
                {topics.map(topic => <p>{topic.name}</p>)}
            </div>
        </div>
    )
}

export default OldTopics
